import * as Immutable from 'immutable'
import {CSSProperties, SyntheticEvent} from "react"


export function shallowEqual(a, b): boolean {
    if (a === b) return true
    if (a == null || b == null) return false
    for (let k in a) {
        if (a[k] !== b[k]) return false
    }
    for (let k in b) {
        if (b[k] !== a[k]) return false
    }
    return true
}


export function assign(target, src) {
    let copy = null
    for (let key in src) {
        if (src[key] === target[key]) continue
        (copy || (copy = {...target}))[key] = src[key]
    }
    return copy || target
}


export function compare(a, b): number {
    if (a === b) return 0
    if (a === undefined) return 1
    if (b === undefined) return -1
    if (a === null) return 1
    if (b === null) return -1
    if (a > b) return 1
    if (a < b) return -1
    return compare(String(a), String(b))
}


export function reportError(e) {
    console.error(e)
}


export function safeCall(f: () => void) {
    try {
        f()
    } catch(e) {
        reportError(e)
    }
}


export function safeCall1<T>(f: (arg: T) => any, arg) {
    try {
        f(arg)
    } catch(e) {
        reportError(e)
    }
}

export function removeArrayItem<T>(arr: T[], item: T): boolean {
    let idx = arr.indexOf(item)
    if (idx < 0) return false
    arr.splice(idx, 1)
    return true
}


function defineMethod(Klass, name, fn) {
    Object.defineProperty(Klass.prototype, name, {value: fn, enumerable: false})
}


defineMethod(Object, '__ti_get_index', function(idx) {
    return this[idx]
})


defineMethod(Immutable.Collection, '__ti_get_index', function(idx) {
    return this.get(idx)
})


type Mapping<K, V> = Immutable.Map<K, V>


export type IndexOf<T> = T extends Array<any>
    ? number
    : T extends Immutable.Record<infer Props>
        ? keyof Props
        : T extends Mapping<infer K, any>
            ? K
            : keyof T


export type ValueOf<T, K> = T extends Array<infer V>
    ? V
    : T extends Immutable.Record<infer Props>
        ? K extends keyof Props ? Props[K] : never
        : T extends Mapping<any, any>
            ? T extends Mapping<K, infer V>  ? V | undefined : never
            : K extends keyof T ? T[K] : never


export function getIndex<T, I extends IndexOf<T>>(obj: T, idx: I): ValueOf<T, I> {
    return (<any><unknown>obj).__ti_get_index(idx)
}


export interface Action {
    name: string,
    event?: SyntheticEvent
}


export interface StyleProperties {
    className?: string
    style?: CSSProperties
}


export function stylesEqual(s1: StyleProperties, s2: StyleProperties): boolean {
    if (s1 === s2) return true
    if (s1.className != s2.className) return false
    return shallowEqual(s1.style, s2.style)
}


export const MODAL_ROOT: HTMLElement = document.getElementById('modal-root') as HTMLElement