import * as React from "react"
import {DownshiftSelect} from "./simple"


let ITEMS: number[] = []
for (let i = 0; i < 10000; i++) {
    ITEMS.push(i)
}


function itemToString(x?: number): string {
    return x == null ? '' : String(x)
}


function itemToMenu(x: number, query: string): React.ReactNode {
    let s = String(x)
    if (!query) return s
    let idx = s.indexOf(query)
    if (idx < 0) return s
    let before = s.slice(0, idx)
    let match = s.slice(idx, idx + query.length)
    let after = s.slice(idx + query.length)
    return <React.Fragment>
        {before}
        <span style={{backgroundColor: "var(--menu-match-background-color)"}}>{match}</span>
        {after}
    </React.Fragment>
}


function getItems(query) {
    return query ? ITEMS.filter(x => String(x).includes(query)) : ITEMS
}


export class DownshiftDemo extends React.Component<{}, {selected?: number}> {
    state = {selected: undefined}

    render() {
        return <div className="ma6" style={{width: 400}}>
            <DownshiftSelect itemToString={itemToString}
                             itemToMenu={itemToMenu}
                             items={getItems}
                             value={this.state.selected}
                             onChange={this.onChange}
            />
        </div>
    }

    onChange = value => {
        this.setState({selected: value})
    }
}