import * as React from 'react'
import {FixedSizeList} from 'react-window'
import Downshift, {ControllerStateAndHelpers} from 'downshift'
import Popper from 'popper.js'
import {Icon, Intent, Classes} from '@blueprintjs/core'
import {StyleProperties, safeCall1} from "../../util"
import {MENU_ITEM_HEIGHT} from "../../styles/vars"


export interface Properties<Item> extends StyleProperties {
    itemToString: (item?: Item) => string
    itemToMenu: (item: Item, query: string) => React.ReactNode
    items: (query: string) => Item[]
    value?: Item
    id?: string
    onChange?: (item?: Item) => void
    onOpenStateChange?: (isOpen: boolean) => void
    intent?: Intent
    disabled?: boolean
    title?: string
}


export class DownshiftSelect<Item> extends React.Component<Properties<Item>> {
    private items: Item[] = []
    private list: React.RefObject<FixedSizeList> = React.createRef()
    private menuContainer: React.RefObject<HTMLDivElement> = React.createRef()
    private inputGroup: React.RefObject<HTMLDivElement> = React.createRef()
    private popper?: Popper

    state = {
        isOpen: false,
        width: 300,
        inputValue: ''
    }

    render() {
        this.items = this.state.isOpen ? this.props.items(this.state.inputValue) : []
        return <Downshift onChange={this.props.onChange}
                          selectedItem={this.props.value === undefined ? null : this.props.value}
                          itemToString={this.props.itemToString}
                          itemCount={this.items.length}
                          onStateChange={this.onStateChange}
                          stateReducer={this.stateReducer}
                          defaultHighlightedIndex={0}>
            {this.view}
        </Downshift>
    }

    private view = options => {
        let groupClass = 'bp3-input-group flex-grow-1'

        if (this.props.intent != null) {
            groupClass += ' ' + Classes.intentClass(this.props.intent)
        }

        return <div className={this.props.className} style={this.props.style}>
            <div className="bp3-control-group">
                <div className={groupClass}
                     title={this.props.title}
                     ref={this.inputGroup}>
                    <input {...options.getInputProps({className: 'bp3-input', id: this.props.id, disabled: this.props.disabled})}/>
                    {this.renderClearButton(options)}
                </div>
                <button {...options.getToggleButtonProps({
                    className: 'bp3-button',
                    tabIndex: -1,
                    disabled: this.props.disabled,
                    onClick: this.onToggleButtonClick
                })}>
                    <Icon icon={options.isOpen ? 'chevron-up' : 'chevron-down'}/>
                </button>
            </div>
            <div className="ti-select-menu elevate-3 pv1"
                 style={{display: options.isOpen ? 'initial' : 'none'}}
                 ref={this.menuContainer}>
                {this.renderMenuList(options)}
            </div>
        </div>
    }

    private renderClearButton(options: ControllerStateAndHelpers<Item>) {
        if (options.selectedItem == null) return null
        return <span className="bp3-input-action">
            <button className="bp3-button bp3-minimal"
                    tabIndex={-1}
                    aria-label="Очистить"
                    onClick={() => {
                        this.focusInput()
                        options.clearSelection()
                    }}>
                <Icon icon="cross"/>
            </button>
        </span>
    }

    private renderMenuList(options: ControllerStateAndHelpers<Item>) {
        if (!options.isOpen) return null
        if (this.items.length == 0) return <div className="ti-select-menu-item ti-select-menu-nothing-item"
                                                style={{width: this.state.width}}>
            Ничего не найдено
        </div>
        return <FixedSizeList ref={this.list}
                              width={this.state.width}
                              height={Math.min(10 * MENU_ITEM_HEIGHT, this.items.length * MENU_ITEM_HEIGHT)}
                              itemCount={this.items.length}
                              itemSize={MENU_ITEM_HEIGHT}
                              itemData={{options}}>
            {this.Item}
        </FixedSizeList>
    }

    private Item = ({index, style, data}) => {
        let options = data.options
        let item = this.items[index]
        let className = 'ti-select-menu-item'
        let active = options.highlightedIndex === index

        if (active) {
            className += ' bp3-active'
        }

        let props = options.getItemProps({
            style,
            index,
            item,
            className,
        })

        return <div {...props}>
            {this.props.itemToMenu(item, options.inputValue)}
        </div>
    }

    private stateReducer = (state, changes) => {
        switch (changes.type) {
            case Downshift.stateChangeTypes.clickButton:
                if (changes.hasOwnProperty('isOpen')) {
                    let inputValue = state.isOpen
                        ? (state.selectedItem == null ? '' : this.props.itemToString(state.selectedItem))
                        : ''
                    return {...changes, inputValue}
                }
                break
            case Downshift.stateChangeTypes.changeInput:
                return {...changes, highlightedIndex: 0}
        }
        return changes
    }

    private onStateChange = (changes, state) => {
        if (changes.hasOwnProperty('highlightedIndex') && this.list.current != null) {
            this.list.current.scrollToItem(changes.highlightedIndex)
        }
        if (changes.hasOwnProperty('isOpen')) {
            this.setState({isOpen: changes.isOpen})
            if (changes.isOpen) {
                this.setState({width: this.measureWidth()})
            }
            if (this.props.onOpenStateChange) {
                safeCall1(this.props.onOpenStateChange, changes.isOpen)
            }
        }
        if (changes.hasOwnProperty('inputValue')) {
            this.setState({inputValue: changes.inputValue || ''})
        }
    }

    private measureWidth() {
        return this.inputGroup.current
            ? this.inputGroup.current.offsetWidth
            : this.state.width
    }

    private onToggleButtonClick = () => {
        if (this.state.isOpen) return
        this.focusInput()
    }

    private focusInput() {
        let inputGroup = this.inputGroup.current as HTMLElement
        let input = inputGroup.firstElementChild as HTMLInputElement
        input.focus()
    }

    componentDidUpdate(prevProps, prevState) {
        this.updateMenuPlacement(this.state.isOpen, prevState.isOpen)
    }

    private updateMenuPlacement(isOpen, wasOpen) {
        if (isOpen === wasOpen) return
        if (isOpen) {
            this.updatePopper()
        } else {
            this.popper && this.popper.disableEventListeners()
        }
    }

    private updatePopper() {
        if (this.popper) {
            this.popper.update()
            this.popper.enableEventListeners()
        } else {
            let menu = this.menuContainer.current as HTMLElement
            let root = menu.parentElement as HTMLElement
            this.popper = new Popper(root, menu, {
                placement: 'bottom-start',
                eventsEnabled: true
            })
        }
    }

    componentWillUnmount() {
        this.popper && this.popper.destroy()
    }
}